<template>
    <div class="login-bg">
        <div class="login-con">
            <h3 class="login-form_title">惠信息管理系统·登录</h3>
            <el-form :model="form" class="login-form">
                <el-form-item>
                    <el-input v-model="form.assest" placeholder="账号"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input v-model="form.psw" placeholder="密码" type="password"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit" class="login-form_btn">登 录</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            form: {
                assest: '',
                psw: ''
            }
        }
    },
    methods:{
        onSubmit() {
            if(!this.form.assest) {
                app.ShowMsg.call(this, '请填写账号')
            } else if (!this.form.psw) {
                app.ShowMsg.call(this, '请填写密码')
            } else {
                this.$post('url', this.form, (data) => {
                    
                })
            }
        }
    },
    mounted:function(){
        console.log(app)
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .login-bg{position:relative; width:100%; height:100%; background:#2d3a4b;}
    .login-con{width:500px; display:inline-block; text-align:center; position:absolute; left:50%; top:40%; transform:translate(-50%,-50%);}

    .login-form >>> .el-input__inner{background:#2d3a4b; color:white;}

    .login-form_title{font-size:26px; margin-bottom:1.5em; color:#eee;}
    .login-form_btn{display:block; width:100%;}
</style>



