<template>
    <el-container style="height:100%;">
        <el-aside
        width="200px">
            <el-menu
            router
            :default-active="$route.fullPath"
            background-color="#304156"
            text-color="rgb(191, 203, 217)"
            active-text-color="#ffd04b">
                <el-menu-item index="/admin/home"><i class="el-icon-location-outline"></i>首页</el-menu-item>
                <el-submenu index="1">
                    <template slot="title"><i class="el-icon-message"></i>用户管理</template>
                    <el-menu-item index="1-1">用户列表</el-menu-item>
                    <el-menu-item index="1-2">订单列表</el-menu-item>
                    <el-menu-item index="/admin/invoice/list">发票管理</el-menu-item>
                </el-submenu>

                <el-submenu index="3">
                    <template slot="title"><i class="el-icon-setting"></i>系统管理</template>
                    <el-menu-item index="3-1">修改密码</el-menu-item>
                    <el-menu-item index="3-2">用户管理</el-menu-item>
                    <el-menu-item index="3-3">管理员权限管理</el-menu-item>
                    <el-menu-item index="3-4-1">管理员账号管理</el-menu-item>
                </el-submenu>
            </el-menu>
        </el-aside>

        <el-container>
            <el-header style="text-align: right; font-size: 12px">
                <el-breadcrumb separator="/" class="el-header_breadcrumb">
                    <template v-for="(item, index) in $store.state.adminSysBreadPath">
                        <el-breadcrumb-item
                        :to="item.path">
                            {{item.name}}
                        </el-breadcrumb-item>
                    </template>
                </el-breadcrumb>

                <el-dropdown>
                    <i class="el-icon-setting" style=""></i>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item>查看</el-dropdown-item>
                        <el-dropdown-item>新增</el-dropdown-item>
                        <el-dropdown-item>删除</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <span>王小虎</span>
            </el-header>

            <el-main>
                <router-view/>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
export default {
    data() {
        return {}
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-header {
    background: white;
    color: #303133;
    line-height: 60px;
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.12), 0 0 3px 0 rgba(0,0,0,.04);
}

.el-aside {}

.el-menu{height:100%;}
.el-main{padding:0;}

.el-header_breadcrumb{float:left; line-height:60px;}

.el-main >>> .page{display:block; height:100%;}
.el-main >>> .page.padding{padding:20px;}
</style>



